<template>
  <section id="experience-section" ref="refExSection">
    <button @click="clickMenu">
      <h1 class="section-title">경력사항</h1>
      <div class="side-menu" ref="refMenuTxt">
        <div class="arrow"></div>
        <div class="menu-txt">click</div>
      </div>
    </button>
    <div class="menu-overlay" ref="refOverlay"></div>
    <div class="ex-menu" ref="refExMenu">
      <div class="line" ref="refLine">
        <div :class="['dot',{'up':(index+1)%2 === 0}]" v-for="(item, index) in workList" :key="index" ref="refDot">
          <div class="hover-pop">
            <div class="pop-wrap">
              <div class="row">
                <div class="col">
                  <span class="title">{{ item.title }}</span>
                </div>
                <div class="col">
                  <button class="txt-btn" v-if="item.detail" @click="openDetail(item.title)">
                    <span>상세보기</span>
                  </button>
                </div>
              </div>
              <div class="row">
                <div class="col">
                  <span class="title">{{ item.startDate }}~{{ item.endDate }}</span>
                  <span class="sub-title m-n">({{ item.period }})</span>
                </div>
              </div>
              <div class="row">
                <div class="col">
                  <span class="title">{{ item.duty }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="detail-section">
      <Izex ref="izexContents"/>
    </div>
  </section>
</template>

<script>
import { ref, onMounted } from 'vue';
import Izex from './WorkExperienceList/Izex.vue';

export default {
  components: {
    Izex,
  },
  setup() {
    let tablet = matchMedia("screen and (max-width: 768px)").matches;
    const workList = [
      // {
      //   title: '아름다운 미소 치과',
      //   startDate: '2016-02',
      //   endDate: '2019-07',
      //   period: '3년 6개월',
      //   duty: '치과위생사',
      //   detail: false,
      // },
      {
        title: '아이젝스',
        startDate: '2020-04',
        endDate: '2023-05',
        period: '3년 2개월',
        duty: '주임 연구원',
        detail: true,
      },
    ];
    const izexContents = ref();

    const openMenu = ref(true);
    
    const refExSection = ref();
    const refMenuTxt = ref();
    const refOverlay = ref();
    const refExMenu = ref();
    const refLine = ref();
    const refDot = ref();
    
    const count = workList.length;
    const interval = 90/count;

    const loadData = async () => {
      // 사이즈 체크
      tablet = matchMedia("screen and (max-width: 768px)").matches;
      // 애니메이션
      await setTimeout(() => {
        if(refLine.value&&refLine.value.style) {
          if(tablet) refLine.value.style.width = 'auto';
          else refLine.value.style.width = '100%';
        }
      }, 100);

      // dot 위치 셋팅
      await refDot.value.forEach((item, idx) => {
        if (idx == 0) item.style.setProperty('--interval', '10%');
        else item.style.setProperty('--interval', `${interval*idx}%`);
        setTimeout(() => {
          item.style.opacity = 1;
        }, 600)
      });

      // 메뉴 애니메이션
      await setTimeout(() => {
        if(refExSection.value&&refExSection.value.style) {
          if(tablet) refExSection.value.style.setProperty('--menuHeight', '100%');
          else {
            refExSection.value.style.setProperty('--menuHeight', '85px');
            refOverlay.value.style.opacity = 1;
            refMenuTxt.value.style.opacity = 1;
          } 
        }
      }, 2000);
    }

    const clickMenu = () => {
      if(tablet) return;
      openMenu.value = !openMenu.value
      if(openMenu.value) {
        refExSection.value.style.setProperty('--menuHeight', '15vh');
        refOverlay.value.style.opacity = 1;
        setTimeout(() => {
          refExMenu.value.style.opacity = 1;
        }, 100)

      } else {
        refExMenu.value.style.opacity = 0;
        setTimeout(() => {
          refExSection.value.style.setProperty('--menuHeight', '0px');
          refOverlay.value.style.opacity = 0;
        }, 100)
      }
    }

    const openDetail = (title) => {
      if(tablet) {
        if(title === '아이젝스') window.open('http://bit.ly/3sroEAu');
      } else {
        if(title === '아이젝스') izexContents.value.openComponent();
        else izexContents.value.closeComponent();
      }
    };

    window.addEventListener("resize", loadData);

    onMounted(() => {
      loadData();
    });

    return {
      izexContents,

      workList,
      refExSection,
      refMenuTxt,
      refOverlay,
      refExMenu,
      refLine,
      refDot,

      clickMenu,
      openMenu,
      openDetail,
    };
  }
}

</script>