        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '근무 환경을 효율적으로 관리하기 위한 솔루션',
          '이 솔루션의 특이점은 캘린더를 사용하며, 일단위/주단위/월단위로 근로자들의 근무시간 현황을 파악할 수 있다는 점입니다.',
          '고객사마다 근무제 정책이 다른 점을 고려하여 소정 근무, 초과 근무, 휴가를 계산하는 점이 까다로웠고, 대시보드 UI가 다양하여 확장성 있는 소스를 작성하기 용이하지 않았다는 점이 기억에 남습니다.',
        ],
      },
      {
        title: 'IPM(네트워크 관리)',
        date: '2022.06 ~ 2022.11',
        img: ipm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          'IP현황을 파악하여 관리 및 통제를 하는 솔루션의 클라이언트 웹서비스',
          '이 솔루션의 특이점은 IP의 할당 및 사용 상태를 한눈에 파악 할 수 있는 IP 매트릭스입니다.',
          '반응형으로 작업해야 되었던 부분과, IP 블록을 다중선택하여 할당 및 사용등 처리를 할 수 있어 신경써서 작업했던 부분이 기억에 남습니다.',
        ],
      },
      {
        title: 'ESM(보안 관리)',
        date: '2022.01 ~ 2022.05',
        img: esm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '기업 정보가 유출되지 않도록 사전에 감시 및 통제를 하는 솔루션의 클라이언트 웹서비스',
          '관리자가 다양한 보안 정책을 설정할 수 있는 관리자 웹 서비스를 개발하였습니다.',
          'IZEX 제품 중 정책 종류가 가장 많은 솔루션으로, 하나의 정책내에서도 세부적으로 설정할 수 있는 종류가 다양하여 이전 프로젝트에 비해 데이터 가공 및 처리가 복잡한 프로젝트였습니다.',
          'policy.js 파일을 만들어 공통되는 state를 관리하며, 소스 가독성에 좀 더 신경 쓰게 된 계기가 된 프로젝트입니다.'
        ],
      },
      {
        title: 'PDM(패치&배포 관리)',
        date: '2021.08 ~ 2021.12',
        img: pdm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '설정한 정책을 통해 사용자PC 업데이트를 제어 및 자동 배포하는 솔루션의 클라이언트 웹서비스',
          '관리자가 지정한 대상에게 특정시점/특정 기간내에 패키지 리스트를 배포할 수 있도록 정책을 제공하는 웹 서비스 입니다.',
          '이 솔루션을 작업하며 가장 기억에 남는 부분은 대용량 파일을 관리하는 부분입니다.',
          '대용량 파일 관리는 npm 패키지 large-file-uploader을 사용했는데 프로젝트의 메인 라이브러리인 DevExpress의 버전을 업그레이드하면서 업로더가 정상적으로 작동되지 않는 이슈가 있었습니다.',
          '이슈의 원인은 찾았으나, 해결 법을 찾지 못해 결국 패키지를 변경하였고 이부분으로 인해 아쉬움이 남는 프로젝트입니다.'
        ],
      },
      {
        title: 'RHM(원격 장애 관리)',
        date: '2021.04 ~ 2021.08',
        img: rhm,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '장애 처리의 편의성을 높이기 위해 작업 단계별 상황을 모니터링할 수 있는 솔루션',
          '지라와 유사한 프로젝트로, 이슈를 등록하면 담당자가 작업을 배정하여 이슈를 관리하는 형태의 웹 서비스입니다.',
          '사전 지식이 필요한 부분이 많지 않아 수월하게 작업을 진행할 수 있었던 프로젝트입니다.'
        ],
      },
      {
        title: 'SAM(소프트웨어 자산 관리)',
        img: sam,
        date: '2020.12 ~ 2021.04',
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '계약한 소프트웨어를 등록하여 한눈에 현황을 파악하고, 고유한 규칙을 정의하여 관리하는 솔루션',
          '소프트웨어의 사용 이력, 접속 이력등을 확인 할 수 있으며 설치 현황, 통제 현황등을 보여줍니다.',
          '솔루션을 진행하면서 가장 기억에 남는 부분은 현황 데이터의 가독성이 용이하도록 아이콘을 사용하면서 생긴 이슈입니다.',
          '계속해서 늘어나는 아이콘을 관리하는 이슈와 케이스별로 툴팁이 있거나 없는경우의 예외 처리, 툴팁이 잘리는 이슈 등의 어려움이 있었으나 아이콘을 폰트로 변경하여 관리하고 툴팁 소스를 GridMixin 파일을 만들어 관리, import하는 등의 해결을 한 부분이 기억에 남는 프로젝트 입니다.',
        ],
      },
      {
        title: 'OAM(실물 자산 관리)',
        date: '2020.08 ~ 2020.12',
        img: oam,
        role: '프론트엔드 개발',
        useItem: [
          'Vue2', 'CSS', 'graphQL', 'VueX', 'DevExpress'
        ],
        description: [
          '자산 상태를 실시간으로 파악하여 자산현황을 확인할 수 있는 솔루션',
          '거래처와 관리자, 품목등을 분류 및 등록하여 계약 및 관리 현황을 한 눈에 볼 수 있습니다.',
          '아이젝스에서 처음으로 Vue를 도입한 프로젝트입니다.',
          '프론트엔드로서의 첫 프로젝트로 관련 용어를 모르는 상태에서 시작하여 많은 어려움을 느꼈지만, 나날이 개발자다워지는 저의 모습을 보며 뿌듯함을 느꼈던 프로젝트입니다.'
        ],
      },
      {
        title: '자사 홈페이지 리뉴얼',
        date: '2022.11 ~ 2023.01',
        img: home,
        role: '퍼블리싱',
        useItem: [
          'PHP', 'jQuery', 'gnuboard', 'cafe24'
        ],
        description: [
          '모바일 페이지 부재 및 디자인 트렌드 변화를 반영하기 위해 리뉴얼을 진행하였습니다.',
          '반응형 페이지로 제작되었습니다.',
        ],
      },
      {
        title: 'NetHelper Messenger',
        date: '2020.06 ~ 2020.07',
        img: msg,
        role: '퍼블리싱',
        useItem: [
          'Vue2', 'SCSS', 'Vuetify'
        ],
        description: [
          '자사 솔루션인 NetHelper에 추가될 업무용 메신저로',
          'Electron 기반의 크로스 플랫폼 데스크톱 앱 프로젝트입니다',
          '메신저 내 채팅, 쪽지, 파일 공유 기능 등이 포함되어 있습니다.'
        ],
      },
      {
        title: 'WTM7.8 (근무시간 관리)',
        date: '2020.04 ~ 2023.05',
        img: wtm78,
        role: '퍼블리싱',
        useItem: [
          'HTML', 'CSS', 'jQuery', 'TOAST UI', 'FullCalendar'
        ],
        description: [
          '근무 환경을 효율적으로 관리하기 위한 솔루션',
          'PC 웹 페이지 유지보수 및 고객사 커스텀을 담당하였습니다.',
          '모바일 페이지 부재로 인해 모바일 페이지 퍼블리싱을 진행하였습니다.',
        ],
      },
    ];
    
    const refScrollWrap = ref();

    // **** component visible ****
    const initVisible = ref(false);
    const openComponent = () => {
      initVisible.value = true;
    };
    const closeComponent = () => {
      initVisible.value = false;
    };

    // **** 랜덤 컬러 ****
    const randomColor = () => {
      let color_r = Math.floor(Math.random() * 127 + 128).toString(16);
      let color_g = Math.floor(Math.random() * 127 + 128).toString(16);
      let color_b = Math.floor(Math.random() * 127 + 128).toString(16);
      return `#${color_r+color_g+color_b}`;
    }

    //  **** 휠 이벤트 ****
    let progress = 0;
    let active = 0;
    let speedWheel = 0.1;

    const handleWheel = e => {
      const wheelProgress = e.deltaY * speedWheel
      progress = Math.max(0, Math.min(progress + wheelProgress, 100));
      active = Math.floor(progress/100*(experienceList.length-1));
      if(refScrollWrap.value && refScrollWrap.value.style) refScrollWrap.value.style.setProperty('--active', `-${active}`);
    };

    //  **** 클릭 이벤트 ****
    const clickLeft = () => {
      if(active === 0) return;
      progress -= 20;
      active -= 1;
      refScrollWrap.value.style.setProperty('--active', `-${active}`);
    };

    const clickRight = () => {
      if(active === experienceList.length-1) return;
      progress += 20;
      active += 1;
      refScrollWrap.value.style.setProperty('--active', `-${active}`);
    };

    onMounted(() => {
      document.addEventListener('mousewheel', handleWheel);
    });

    onUnmounted(() => {
      document.removeEventListener('mousewheel', handleWheel);
    });

    // **** popup *****
    let popDetail = ref({});
    const contentPopVisible = ref(false);
    const contentPopOpen = (item) => {
      document.removeEventListener('mousewheel', handleWheel);
      popDetail.value = item;
      contentPopVisible.value = true;
    };
    const contentPopClose = () => {
      document.addEventListener('mousewheel', handleWheel);
      popDetail.value = {};
      contentPopVisible.value = false;
    };

    return {
      experienceList,
      refScrollWrap,
      initVisible,
      openComponent,
      closeComponent,
      randomColor,
      clickLeft,
      clickRight,
      popDetail,
      contentPopVisible,
      contentPopOpen,
      contentPopClose,
    };
  }
}
</script>